<template>
  <div class="cms-list-layout">
    <cms-prompt collapse="true">
      <li>展示用户的投诉信息</li>
      <li>可对投诉信息进行处理，结果将发送至用户信息</li>
    </cms-prompt>
    <section class="cms-list-query">
      <div class="query-group">
        <label>商品名称</label>
        <el-input v-model="queryParams['product.name']" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>店铺名称</label>
        <el-input v-model="queryParams['store.storeName']" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>处理状态</label>
        <el-select v-model="queryParams.status" @change="query">
          <el-option label="全部" value></el-option>
          <el-option label="未处理" :value="1"></el-option>
          <el-option label="已处理" :value="2"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>提交时间</label>
        <cms-picker v-model="timer" @change="setTimer"></cms-picker>
      </div>
      <div class="query-group">
        <label>投诉类型</label>
        <el-select v-model="queryParams.reportDictType" @change="query">
          <el-option label="全部" value></el-option>
          <el-option label="虚假宣传" :value="1"></el-option>
          <el-option label="信息有误" :value="2"></el-option>
          <el-option label="违禁品" :value="3"></el-option>
          <el-option label="侵权" :value="4"></el-option>
          <el-option label="价格违规" :value="5"></el-option>
          <el-option label="政治有害" :value="6"></el-option>
          <el-option label="滥发信息" :value="7"></el-option>
          <el-option label="其他" :value="8"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker="true" @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 数据列表 -->
    <el-table
      :data="tableData"
      style="width: 100%"
      stripe
      v-loading="loading"
      @selection-change="getCheckItems"
      @row-click="checkRows"
      ref="dataTable"
    >
      <el-table-column prop="product.name" label="投诉商品" min-width="300">
        <template slot-scope="scope">
          <!-- 商品信息 -->
          <div v-if="scope.row.product" class="comment__inner">
            <div style="display:inline-block;width:60px;height:60px;">
              <img
                v-if="scope.row.product.resources[0]"
                class="product-img"
                :src="$cms.filterUrl(scope.row.product.resources[0].imageResouce.url)"
              >
            </div>
            <div style="display:inline-block;width:80%;">
              <h3
                style="display:inline-block;width:100%;position:relative;top:10px;line-height:24px;"
                class="comment-title-h text-overflow"
                :title="scope.row.product.name"
              >{{scope.row.product.name}}</h3>
              <p
                v-if="scope.row.product.skuAttrs"
                style="display:inline-block;width:230px;color:#666;line-height:20px;"
                class="comment-title-h text-overflow"
                :title="showAttr(scope.row.product.skuAttrs)"
              >
                <span
                  style="font-size:12px; padding-left:3px;"
                  v-if="scope.row.product.skuAttrs.length>0"
                >规格：</span>
                <span
                  style="font-size:12px; padding-left:3px;"
                  v-for="(item,index) in scope.row.product.skuAttrs"
                  :key="index"
                >{{item.attrName}}:{{item.attrValueName}}</span>
              </p>
            </div>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="reportDictType" label="投诉类型" min-width="60">
        <template slot-scope="scope">
          <span>{{scope.row.reportDictType | typeFilter}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="specificDescription" label="描述" min-width="240">
        <template slot-scope="scope">
          <div
            class="text-overflow"
            :title="scope.row.specificDescription"
          >{{scope.row.specificDescription}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="status" label="状态" min-width="60">
        <template slot-scope="scope">
          <span v-if="scope.row.status===1">未处理</span>
          <span v-if="scope.row.status===2">已处理</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="240" fixed="right" align="center">
        <div slot-scope="scope" style="text-align:center;">
          <cms-button
            icon="edit"
            size="small"
            type="info"
            @click.stop="showDialog(scope.row.id,'nosolve')"
          >详情</cms-button>
          <cms-button
            v-if="scope.row.status==1"
            icon="edit"
            size="small"
            type="info"
            @click.stop="showDialog(scope.row.id,'solve')"
          >处理</cms-button>
        </div>
      </el-table-column>
    </el-table>
     <cms-pagination @change="getPage" :total="total"></cms-pagination>
    <!-- 投诉处理 -->
    <el-dialog title="投诉处理" :visible.sync="interestsDialog" width="50%">
      <el-form ref="form" :rules="rules" :model="details" label-width="140px">
        <div style="padding-right:20px;">
          <el-form-item label="投诉商品:">
            <div v-if="details.product" style="display:inline-block;width:60px;height:60px;">
              <img
                v-if="details.product.resources[0]"
                class="product-img"
                :src="$cms.filterUrl(details.product.resources[0].imageResouce.url)"
              >
            </div>
            <span class="complaint-title" v-if="details.product">{{details.product.name}}</span>
          </el-form-item>
          <el-form-item label="投诉类型:">
            <span>{{details.reportDictType | typeFilter}}</span>
          </el-form-item>
          <el-form-item label="描述:">
            <span class="comment-title">{{details.specificDescription}}</span>
          </el-form-item>
          <el-form-item label="附图:">
            <cms-picture-zone :data="details.spaceDatas" :disabled="true"></cms-picture-zone>
          </el-form-item>
          <el-form-item label="状态:">
            <span v-if="details.status==1">未处理</span>
            <span v-if="details.status==2">已处理</span>
          </el-form-item>
          <el-form-item label="提交时间:">
            <span>{{$moment(details.createTime)}}</span>
          </el-form-item>
          <el-form-item
            ref="result"
            style="margin-bottom:20px;"
            class="com-result"
            label="处理结果:"
            prop="dealResult"
            :rules="details.status==1?[{ required: true, message: '请输入处理结果', trigger: 'blur' }]:[]"
          >
            <span style="word-break: break-all;" v-if="details.status==2">{{details.dealResult}}</span>
            <el-input
              v-if="isSolve && details.status!=2"
              type="textarea"
              placeholder="处理结果在0-255个字符之间"
              maxlength="255"
              v-model="details.dealResult"
            ></el-input>
          </el-form-item>
        </div>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button
          type="primary"
          v-if="details.status ===1 && isSolve"
          @click="submit(details.id)"
        >提交</el-button>
        <el-button @click="cancel">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import list from '@/mixins/list'
import {
  sysComplaintPage,
  sysComplaintGet,
  sysComplaintUpdate
} from '@/api/marketing/index.js'
var defaultData = {
  'store.storeName': '',
  'product.name': '',
  status: '',
  reportDictType: '',
  page: 1,
  size: 10,
  startTime: '',
  endTime: ''
}
export default {
  mixins: [list],
  data() {
    return {
      interestsDialog: false,
      queryParams: Object.assign({}, defaultData),
      timer: [],
      details: {}, // 详情
      isSolve: false, // 控制处理按钮
      rules: {
        dealResult: [
          { required: true, message: '请输入处理结果', trigger: 'blur' }
        ]
      }
    }
  },
  filters: {
    typeFilter(val) {
      const typeArr = [
        '',
        '虚假宣传',
        '信息有误',
        '违禁品',
        '侵权',
        '价格违规',
        '政治有害',
        '滥发信息',
        '其他'
      ]
      return typeArr[val]
    }
  },
  methods: {
    showAttr(val) {
      let attr = ''
      val.forEach(v => {
        attr += v.attrName + ':' + v.attrValueName
      })
      return attr
    },
    cancel() {
      this.interestsDialog = false
      this.isSolve = false
      this.details.dealResult = ''
      this.$refs['result'].clearValidate()
    },
    submit(id) {
      this.$refs['form'].validate(valid => {
        if (valid) {
          sysComplaintUpdate({
            id: id,
            dealResult: this.details.dealResult
          }).then(res => {
            if (res.code === 200) {
              this.$successMessage('提交成功')
              this.getTableData(sysComplaintPage, this.queryParams)
              this.interestsDialog = false
            } else {
              this.$errorMessage(res.message)
            }
          })
        } else {
          return false
        }
      })
    },
    setTimer(timer) {
      this.queryParams.startTime = timer[0]
      this.queryParams.endTime = timer[1]
      this.query()
    },
    showDialog(id, type) {
      if (type === 'solve') {
        // 处理按钮直接显示
        this.isSolve = true
      }
      sysComplaintGet({ id: id }).then(res => {
        this.interestsDialog = true
        if (res.code === 200) {
          this.details = res.data
        } else {
          this.$errorMessage(res.message)
        }
      })
    }
  },
  created() {
    this.getTableData(sysComplaintPage, this.queryParams)
  }
}
</script>
<style scoped>
.complaint-title {
  max-width: 486px;
  display: inline-block;
  vertical-align: top;
  line-height: 22px;
}
</style>



